import { ReactComponent as Spinner } from '../svg/spinner.svg';

const FormButton = ({ notFound, loading }) => {
  if (loading) {
    return (
      <button class='bg-gray-600 font-bold  text-white px-5 w-28'>
        <Spinner />
      </button>
    );
  } else if (notFound) {
    return (
      <button class='bg-gray-800 font-bold  text-gray-100 px-5 w-28'>
         
      </button>
    );
  } else {
    return (
      <button class='bg-gray-600 font-bold  text-gray-200 px-5 w-28'>
        
      </button>
    );
  }
};

export default FormButton;
