import FormButton from './FormButton';

const WeatherForm = ({
  submitHandler,
  inputValue,
  setInputValue,
  notFound,
  loading,
}) => {
  return (
    <div className='mt-6 md:px-20 '>
      <form onSubmit={submitHandler} className='flex justify-center'>
        <FormButton notFound={notFound} loading={loading} />
        <input
          type='text'
          value={inputValue}
          placeholder='    '
          onChange={e => setInputValue(e.target.value)}
          className='
           text-gray-800 text-center py-3 border bg-gray-50 focus:bg-white border-white outline-none'
        />
      </form>
    </div>
  );
};

export default WeatherForm;
