import WeatherIcon from './WeatherIcon';

const WeatherCard = ({ icon, city, description, temp }) => {
  return (
    <div className='relative w-screen m-6  text-center md:px-36'>
      <div className='m-auto md:w-80  bg-gradient-to-b from-gray-50 via-gray-50 to-gray-300 flex flex-col justify-center items-center text-center p-6'>
        <div className='text-md font-bold flex flex-col text-gray-900'>
          <span className='text-4xl'>{city}</span>
        </div>
        <div className='mt-6 mb-2 w-32 h-32 flex items-center justify-center'>
          <WeatherIcon icon={icon} />
        </div>
        <p className='text-gray-700 mb-5 font-bold text-lg'>{description}</p>
        <p className='ml-6 text-5xl font-bold mb-3'>
          {temp}
          <span></span>
        </p>
      </div>
    </div>
  );
};

export default WeatherCard;
